body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: 'Titillium Web', sans-serif;
    background-color: #171717;
    color: #FFFFFF;
    overflow: hidden;  /* Prevent page scrolling */
  }
  
  .app-container {
    display: flex;
    flex-direction: column;
  }
  
  header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    background-color: #171717;
    padding: 10px;
  }
  
  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: left;
  }
  
  .back-button {
    background: none;
    border: none;
    padding: 0;
    margin-left: 5px;
  }
  
  .search-icon {
    background: none;
    border: none;
    padding: 0;
    margin-right: 25px;
    margin-top: 10px;
  }
  
  .back-button img, .search-icon img {
    width: 24px;
    height: 24px;
    margin-top: 10px;
  }
  
  h1 {
    margin: 0;
    font-size: 1.5em;
    flex-grow: 1;
    text-align: left;
    font-weight: normal;
    margin-left: 15px;
    margin-top: 10px;
  }
  
  .search-bar-container {
    width: 25%;
    background-color: #171717;
    margin-left: 225px;
    margin-top: -45px;
    height: 5%;
  }
  
  .search-input {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
  }
  
  /* This ensures the grid-scroll-container scrolls independently */
  .grid-scroll-container {
    margin-top: 80px; /* Make space for header and search bar */
    height: calc(100vh - 80px);  /* Full height minus the header */
    overflow-y: auto; /* Enable vertical scrolling */
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
  }
  
  .grid-scroll-container::-webkit-scrollbar {
    display: none;  /* Chrome, Safari and Opera */
  }
  
  .grid-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    padding: 10px;
  }
  
  .grid-item img {
    width: 100%;
    height: auto;
    aspect-ratio: 2/3;
    object-fit: cover;
  }
  
  .grid-item p {
    margin-top: 8px;
    font-size: 15px;
    text-align: left;
    margin-left: 5px;
  }
  